<template>
  <div class="app">
    <div class="containter">
      <div class="preview">
        <img src="./assets/cjb.jpg" class="ico">
        <div class="info">
          <p class="desc">3级传家宝·人&nbsp;&nbsp;135级</p>
          <div>
            <span>星级：</span>
            <span class="star" v-for="item in entityStars" :key="'key'+item">★</span>
            <span class="star" v-for="item in blankStars" :key="'key2'+item">✰</span>
          </div>
        </div>
      </div>
      <div class="desc-item">
        <ul class="list-style" :class="color">
          <li v-for="item in skills" :key="item.skill">【{{ item.type }}】{{ item.skill }} 等级+{{ item.level }}</li>
        </ul>
      </div>
    </div>
    <div class="score">洗炼积分：{{ score }}</div>
    <div class="action">
      <div>
        <label>职业组合：</label>
        <select v-model="name" @change="changeName">
          <option v-for="item in names" :key="item.code" :value="item.code">{{ item.name }}</option>
        </select>
      </div>
      <div v-if="score &gt; 20000">
        <label>自动洗炼：</label>
        <select v-model="auto">
          <option v-for="item in autos" :key="item.code" :value="item.code">{{ item.name }}</option>
        </select>
      </div>
      <div v-if="!auto">
        <button @click="wash">洗练</button>
      </div>
      <div v-if="auto">
        <label>目标技能：</label>
        <select v-model="targetSkill">
          <option v-for="item in targetSkills" :key="item" :value="item">{{ item }}</option>
        </select>
        <span>等级：</span>
        <select v-model="targetLevel">
          <option v-for="item in targetLevels" :key="item" :value="item">{{ item }}</option>
        </select>
      </div>
      <div v-if="auto">
        <label>目标技能：</label>
        <select v-model="targetSkill2">
          <option v-for="item in targetSkills" :key="item" :value="item">{{ item }}</option>
        </select>
        <span>等级：</span>
        <select v-model="targetLevel2">
          <option v-for="item in targetLevels" :key="item" :value="item">{{ item }}</option>
        </select>
      </div>
      <div v-if="auto">
        <label>技能数量：</label>
        <select v-model="targetSkillCount">
          <option v-for="item in targetSkillCounts" :key="item" :value="item">{{ item }}</option>
        </select>
      </div>
      <div v-if="auto">
        <label>满足条件：</label>
        <select v-model="targetType">
          <option v-for="item in targetTypes" :key="item" :value="item">{{ item }}</option>
        </select>
      </div>
      <div v-if="auto">
        <button @click="autoWash">开始自动洗炼</button>
        <button @click="stop" style="margin-left: 10px">停止</button>
      </div>

      <ul class="list-style remark">
        <li>说明：</li>
        <li>1. 洗炼积分超2万分，可设置条件后进行自动洗炼</li>
        <li>2. 手动洗炼出稀有词条或7个及以上技能后，继续洗炼会有提示</li>
        <li>3. 自动洗炼模式下，只有洗出所设置的条件，才会停止洗炼</li>
        <li>4. 欢迎提需求或反馈bug，联系人蘑菇</li>
      </ul>
      
    </div>
  </div>
</template>

<script>

import data from './assets/data.json'

export default {
  name: 'App',
  data () {
    return {
      data,
      score: 0,
      entityStars: 6,
      blankStars: 2,
      skills: [
        { type: '射', skill: '流火', level: 6 },
        { type: '狂', skill: '当康 · 击', level: 7 },
        { type: '狂', skill: '烛龙 · 生', level: 6 }
      ],
      color: 'yellow',
      name: 'sheshou,zhankuang',
      names: [
        { code: 'sheshou,zhankuang', name: '射手-战狂' },
        { code: 'jiayu,zhankuang', name: '甲鱼-战狂' },
        { code: 'sheshou,jiayu', name: '射手-甲鱼' },
        { code: 'meizhe,yiren', name: '魅者-异人' },
        { code: 'naima,fangshi', name: '奶妈-方士' },
        { code: 'yanshi,huahun', name: '偃师-画魂' },
        { code: 'yingling,dieke', name: '影灵-蝶客' },
        { code: 'daoke,xiake', name: '刀客-侠客' }
      ],
      auto: false,
      autos: [
        { code: true, name: '是' },
        { code: false, name: '否' }
      ],

      targetSkills: [],
      targetLevels: ['8', '9', '>=8'],
      
      targetSkill: '',
      targetSkill2: '',

      targetLevel: '8',
      targetLevel2: '8',

      targetSkillCount: 8,
      targetSkillCounts: [1, 2, 3, 4, 5, 6, 7, 8],

      targetType: '满足任意一个',
      targetTypes: ['满足任意一个', '满足全部'],
      
      timer: null,
    }
  },

  created () {
    this.setAutoInit()
    this.clearTimer()
  },

  beforeDestroy () {
    this.clearTimer()
  },
  
  methods: {

    // 清除定时器
    clearTimer () {
      clearInterval(this.timer)
      this.timer = null
    },

    // 设置自动洗炼初始化信息
    setAutoInit () {
      let name1 = this.name.split(',')[0]
      let name2 = this.name.split(',')[1]
      this.targetSkills = [...this.data[name1], ...this.data[name2]]
      this.targetSkill = this.targetSkills[0]
      this.targetSkill2 = this.targetSkills[1]
    },

    // 获取技能条数
    getSkillCount () {
      // 在1-10000之间随机获取1个数
      let num = this.getNum(0, 10000)

      // 技能数量
      let skillCount = 0

      // 洗出1个技能的概率：19.89%
      if (num <= 1989) {
        skillCount = 1
      } 
      
      // 洗出2个技能的概率：31.16%
      else if (num > 1989 && num <= (1989 + 3116)) {
        skillCount = 2
      }

      // 洗出3个技能的概率：31.16%
      else if (num > (1989 + 3116) && num <= (1989 + 3116 + 2652)) {
        skillCount = 3
      }

      // 洗出4个技能的概率：13.26%
      else if (num > (1989 + 3116 + 2652) && num <= (1989 + 3116 + 2652 + 1326)) {
        skillCount = 4
      }

      // 洗出5个技能的概率：7.29%
      else if (num > (1989 + 3116 + 2652 + 1326) && num <= (1989 + 3116 + 2652 + 1326 + 729)) {
        skillCount = 5
      }

      // 洗出6个技能的概率：1.54%
      else if (num > (1989 + 3116 + 2652 + 1326 + 729) && num <= (1989 + 3116 + 2652 + 1326 + 729 + 154)) {
        skillCount = 6
      }

      // 洗出7个技能的概率：0.30%
      else if (num > (1989 + 3116 + 2652 + 1326 + 729 + 154) && num <= (1989 + 3116 + 2652 + 1326 + 729 + 154 + 30)) {
        skillCount = 7
      }

      // 洗出8个技能的概率：0.03%
      else if (num > (1989 + 3116 + 2652 + 1326 + 729 + 154 + 30) && num <= (1989 + 3116 + 2652 + 1326 + 729 + 154 + 30 + 3)) {
        skillCount = 8
      }
      
      // 洗出技能数在1-4之间，为黄色
      if (skillCount < 5) {
        this.color = 'yellow'
      } else if(skillCount >= 7) {
        this.color = 'red'
      } else {
        this.color = 'blue'
      }

      return skillCount
    },

    // 是否达成自动洗炼条件
    getAutoCondition () {
      let rareSkill1 = this.skills.find(item => {
        if (item.skill == this.targetSkill && item.level == this.targetLevel) {
          return true
        }
        if (this.targetLevel == '>=8') {
          if (item.skill == this.targetSkill && item.level >= 8) {
            return true
          }
        }
      })

      let rareSkill2 = this.skills.find(item => {
        if (item.skill == this.targetSkill2 && item.level == this.targetLevel2) {
          return true
        }
        if (this.targetLevel2 == '>=8') {
          if (item.skill == this.targetSkill2 && item.level >= 8) {
            return true
          }
        }
      })

      let targetSkillCount = this.skills.length >= this.targetSkillCount

      let conditions = ''
      if (this.targetType == '满足任意一个') {
        conditions = rareSkill1 || rareSkill2 || targetSkillCount
      }
      if (this.targetType == '满足全部') {
        conditions = rareSkill1 && rareSkill2 && targetSkillCount
      }
      return conditions
    },

    // 是否达成稀有洗炼条件
    getRareTarget () {
      let rareSkills = [
        '护心胆',
        '迷仙引',
        '腹语真言',
        '呆若木石',
        '重明 · 斩',
        '司怪 · 困',
        '蝎尾',
        '苍龙破封',
        '落墨痕',
        '庄周梦蝶',
        '星河转',
        '蝶 · 点雪',
        '眩晕陷阱',
        '天涯比邻',
        '雄心诀',
        '雄心诀'
      ]
      let rareSkill = this.skills.find(item => {
        if (rareSkills.includes(item.skill) && item.level > 8) {
          return true
        }
      })

      let targetSkillCount = this.skills.length >= 7

      return rareSkill || targetSkillCount
    },

    // 洗炼
    wash () {

      // 如果开启自动洗炼
      if (this.auto) {
        let condition = this.getAutoCondition()
        if(condition) {
          this.clearTimer()
          let res = confirm('已洗出目标组合，确认还要继续吗？')
          if (!res) {
            return
          } else {
            this.autoWash()
          }
        }
      } 
      
      // 如果没有开启
      else {
        let rareTarget = this.getRareTarget()
        if(rareTarget) {
          let res = confirm('已洗出稀有词条，确认还要继续吗？')
          if (!res) {
            return
          }
        }
      }

      let data = this.clone(this.data)

      // 获取技能条数
      let skillCount = this.getSkillCount()

      // 随机洗出N条魅者技能
      let skillMeizhe = this.getNum(0, skillCount)
      // 则异人的技能的条数为总数-魅者技能
      let skillYiren = skillCount - skillMeizhe

      let num2 = this.getNum(0, 100)
      let skillLevel = ''

      this.skills = []
      let singleNames = [
        { code: 'meizhe', name: '魅' },
        { code: 'yiren', name: '异' },
        { code: 'naima', name: '奶' },
        { code: 'fangshi', name: '方' },
        { code: 'yanshi', name: '偃' },
        { code: 'huahun', name: '画' },
        { code: 'sheshou', name: '射' },
        { code: 'zhankuang', name: '狂' },
        { code: 'yingling', name: '影' },
        { code: 'dieke', name: '蝶' },
        { code: 'daoke', name: '刀' },
        { code: 'xiake', name: '侠' },
        { code: 'jiayu', name: '甲' }        
      ]

      // 洗魅者技能
      for (let i = 0; i < skillMeizhe; i++) {
        
        // 6星：57%
        if (num2 <= 57) {
          this.entityStars = 6
          this.blankStars = 2
          skillLevel = this.getNum(4, 5)
        }
        
        // 7星：29%
        else if (num2 > 57 && num2 <= (57 + 29)) {
          this.entityStars = 7
          this.blankStars = 1
          skillLevel = this.getNum(6, 7)
        }

        // 8星：14%
        else {
          this.entityStars = 8
          this.blankStars = 0
          skillLevel = this.getNum(8, 9)
        }

        let skillMeizheList = data[this.name.split(',')[0]]
        // 技能index
        let skillIndex = this.getNum(0, skillMeizheList.length - 1)
        
        // 哪个技能
        let skill = skillMeizheList[skillIndex]
        skillMeizheList.splice(skillIndex, 1)
      
        this.skills.push({
          type: singleNames.find(item => item.code == this.name.split(',')[0]).name,
          skill,
          level: skillLevel
        })
      }

      // 洗异人技能
      for (let i = 0; i < skillYiren; i++) {
        // 6星：57%
        if (num2 <= 57) {
          this.entityStars = 6
          this.blankStars = 2
          skillLevel = this.getNum(4, 5)
        }
        
        // 7星：29%
        else if (num2 > 57 && num2 <= (57 + 29)) {
          this.entityStars = 7
          this.blankStars = 1
          skillLevel = this.getNum(6, 7)
        }

        // 8星：14%
        else {
          this.entityStars = 8
          this.blankStars = 0
          skillLevel = this.getNum(8, 9)
        }

        let skillYirenList = data[this.name.split(',')[1]]
        // 技能index
        let skillIndex = this.getNum(0, skillYirenList.length - 1)
        
        // 哪个技能
        let skill = skillYirenList[skillIndex]
        skillYirenList.splice(skillIndex, 1)
      
        this.skills.push({
          type: singleNames.find(item => item.code == this.name.split(',')[1]).name,
          skill,
          level: skillLevel
        })
      }
      this.score += 30

      // console.log('洗出的技能组合：', this.clone(this.skills))
    },

    // 自动洗炼
    autoWash () {
      this.timer = setInterval(() => {
        console.log('正在洗练')
        this.wash()
      }, 800)
    },

    // 停止自动洗炼
    stop () {
      this.clearTimer()
    },

    // 选择职业组合
    changeName () {
      this.score = 0
      this.setAutoInit()
    },

    clone (obj) {
      return JSON.parse(JSON.stringify(obj))
    },

    getNum (min, max) {
      return Math.floor(Math.random() * (max - min + 1)) + min
    }
  }
}
</script>

